.barrage-container {
  position: absolute;
  width: 100%;
  height: 100px;
  overflow: hidden;
  z-index: 1000;
  pointer-events: none;
}

.barrage {
  position: absolute;
  white-space: nowrap;
  font-size: 30px;
  color: #ec8282;
  animation: moveBarrage1 10s linear infinite;
}

@keyframes moveBarrage1 {
  from {
    right: -100%;
  }
  to {
    right: 100%;
  }
}
