* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

.youth-container {
  background-color: #a8cbee;
}
::after,
::before {
  transition: 0.3s;
}

.part-content {
  display: flex;
  width: 100%;
  height: 100vh;
  border: 30px #6aa8ec solid;
  border-radius: 10px;
  position: relative;
}

.part-content::after {
  content: '';
  width: 100px;
  height: 200vh;
  position: absolute;
  top: -100vh;
  right: 230px;
  border: 100px solid #fff;
  z-index: -999;
}

.part-content-l,
.part-content-r {
  position: relative;
  transform-origin: center;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.part-content-l {
  width: 40%;
  background-color: #6aa8ec;
  flex-direction: column;
  color: #fff;
}

.part-content-r {
  flex-wrap: wrap;
  width: 60%;
  background-color: #b4e0fcb4;
}

.part-content-l h2 {
  font: 200 60px '';
  font-family: 'Fredoka One', sans-serif;
  position: relative;
  top: 3%;
}

.part-content-l span {
  color: #0c4888;
  font: 100 20px 'Gochi Hand';
  line-height: 2;
  transition: 0.3s;
}

.part-content-l h2,
.part-content-l span {
  z-index: 999;
  width: 80%;
  transform: translateY(-30px);
}

.part-circle {
  width: 800px;
  height: 800px;
  position: absolute;
  bottom: -440px;
  left: -440px;
  border-radius: 50%;
  border: 43px #fff dashed;
  animation: rotate 100s infinite linear;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.part-img {
  perspective: 800px;
  position: relative;
  width: 50%;
  height: 50%;
  transform-style: preserve-3d;
  display: flex;
  justify-content: center;
  align-items: center;
}

.part-img:nth-child(1) {
  transform: scale(1.3) translate(0px, 80px);
}

.part-img:nth-child(2) {
  transform: translate(-120px, 100px);
}

.part-img:nth-child(3) {
  transform: translate(120px, -100px);
}

.part-img:nth-child(4) {
  transform: scale(1.3) translate(-0px, -80px);
}

.part-img:nth-child(3) img {
  transform: rotateY(-35deg);
}

.part-img:nth-child(4) img {
  transform: rotateY(-35deg);
}

.part-img img {
  transform: rotateY(35deg);
  position: relative;
  width: 60%;
  height: 90%;
  border: 10px #fff solid;
  box-shadow: 10px 20px 28px #0c48888c;
}

.part-img img:hover {
  border: 10px #71aeee dashed;
}
.recording-youth {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.part-title2 {
  font-size: 30px;
  margin: 40px;
  text-align: center;
  color: #71aeee;
  letter-spacing: 10px;
}

.part-line {
  width: 80vw;
  border: #226c9cc4 10px solid;
  margin: 0 auto;
  border-radius: 10px;
  margin-bottom: 30px;
  margin-top: 40px;
}
.part-video {
  width: 90vw;
  display: flex;
  justify-content: center;
}
.part-video .video-box {
  width: 100%;
  height: 70%;
  margin: 0 100px;
  text-align: center;
  border: 10px solid rgb(79, 207, 246);
  border-radius: 10px;
}
.video-box video {
  width: 100%;
}
.part-video + .part-video {
  margin-top: 60px;
  margin-bottom: 30px;
}
.youth-content .content {
  width: 100vw;
}
.youth-content .content img {
  max-width: 75%;
  display: block;
  margin: 0 auto;
  border-radius: 20px;
}

.youth-content .content {
  width: 90%;
  margin: 50px auto;
  border-radius: 20px;
  padding: 30px;
  letter-spacing: 5px;
}
.content .active {
  font-size: 30px;
}

.youth-content .content h1 {
  margin: 0;
  color: #8fbff3;
}

.youth-content .content small {
  color: #555;
  font-style: italic;
}

.youth-content .content p {
  color: #333;
  line-height: 1.5;
}
.toYouth {
  margin: 10px auto;
  height: 300px;
  width: 80%;
  text-align: center;
  border-radius: 10px;
  border: 10px solid rgb(140, 186, 247);
}

.toYouth h1 {
  color: #6aa8ec;
  letter-spacing: 5px;
  font-size: 40px;
}

.toYouth .content {
  margin-top: 20px;
  box-sizing: border-box;
  padding: 15px 70px;
  text-align: justify;
}

.toYouth .content span {
  color: #6aa8ec;
  font-size: 18px;
  letter-spacing: 4px;
  line-height: 1.4;
  opacity: 1;
}
