#story {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(#3671a5, #1e7bcc 50%, #fff 50%, #fff 100%);
  margin-top: 50px;
}

.story-container {
  width: 1100px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.story-container .story {
  position: relative;
  width: 320px;
  background: #fff;
  padding: 100px 40px 60px;
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.1);
}
.story-container .story:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #616ee6;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.5s;
}
.story-container .story:hover:before {
  transform: scaleY(1);
  transform-origin: bottom;
  transition: transform 0.5s;
}
.story-container .story h2 {
  position: absolute;
  left: 40px;
  top: 60px;
  font-size: 4em;
  font-weight: 800;
  z-index: 1;
  opacity: 0.1;
  transition: 0.5s;
}
.story-container .story:hover h2 {
  opacity: 1;
  color: #fff;
  transform: translateY(-40px);
}
.story-container .story h3 {
  position: relative;
  font-size: 1.5em;
  z-index: 2;
  color: #333;
  transition: 0.5s;
}
.story-container .story p {
  position: relative;
  z-index: 2;
  color: #555;
  transition: 0.5s;
  font-size: 18px;
  line-height: 1.3;
}
.story-container .story:hover h3,
.story-container .story:hover p {
  color: #fff;
}
