.dream-content-container {
  background-color: rgb(216, 154, 154);
  height: 500px;
  width: 80vw;
  text-align: center;
  margin: 0 auto;
  border-radius: 30px;
  margin-top: 40px;
}
.dream-content-container h1 {
  padding-top: 30px;
  padding-bottom: 30px;
  color: rgb(107, 69, 69);
}
.dream-content {
  margin: 0 80px;
}
.dream-content .dream-text {
  color: #080909;
  font: 100 20px 'Gochi Hand';
  line-height: 2;
  text-align: justify;
}
