.carousel-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: linear-gradient(
    to bottom right,
    #91defe,
    #99c0f9,
    #bdb6ec,
    #d7b3e3,
    #efb3d5,
    #f9bccc
  );
}

.carousel {
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 15px;
  box-shadow: 10px 5px 50px rgba(122, 39, 39, 0.39);
}

.carousel-images {
  width: 500%;
  height: 100%;
  display: flex;
  position: absolute;
  left: 0;
  transition: 0.2s;
}

.carousel-img {
  width: 100%;
  background-position: center;
  background-size: cover;
}

.min {
  display: flex;
  justify-content: space-evenly;
  position: absolute;
  bottom: 40px;
  width: 30%;
  z-index: 10;
  left: 50%;
  transform: translateX(-50%);
}

.m {
  width: 20px;
  height: 20px;
  cursor: pointer;
  border-radius: 50%;
  border: solid rgba(255, 255, 255, 0.5) 5px;
  background-color: #fff;
}

.carousel-button {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: space-between;
  user-select: none;
}

.carousel-button-left,
.carousel-button-right {
  font-size: 50px;
  background-color: rgba(126, 165, 238, 0.288);
  padding: 0 20px;
  cursor: pointer;
  line-height: 700px;
  color: #fff;
}
.carousel-button-left:hover,
.carousel-button-right:hover {
  background-color: rgba(126, 165, 238, 0.8);
}
.bg {
  width: 100vw;
  height: 100vh;
  z-index: -9999;
  position: absolute;
  filter: blur(12px);
  opacity: 0.5;
}
.m.active {
  background-color: rgb(85, 126, 238); /* 高亮颜色，可以根据需要调整 */
}
