.card {
  height: 25vh;
  display: flex;
  justify-content: center;
}
.card-container {
  max-width: 100vw;
  display: flex;
  flex-wrap: wrap;
}

.card-item {
  position: relative;
  width: 350px;
  height: 190px;
  margin: 15px;
  perspective: 600px;
}
.card-img {
  position: absolute;
  transform-origin: right;
  transform: rotateY(0deg);
  opacity: 1;
  transition: 0.5s;
}

.card-caption {
  position: absolute;
  z-index: 0;
  background-color: #2e97ec;
  color: #fff;
  width: 100%;
  height: 100%;
  padding: 0 20px;
  box-sizing: border-box;
  letter-spacing: 2px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transform-origin: left;
  transform: rotateY(90deg);
  opacity: 0;
  transition: 0.5s;
}

.card-item:hover .card-img {
  transform: rotateY(-90deg);
  opacity: 0;
}

.card-item:hover .card-caption {
  transform: rotateY(0deg);
  opacity: 1;
}

.card-caption h3 {
  font-size: 22px;
  margin-bottom: 15px;
}

.card-caption p {
  font-size: 14px;
}
