.shell{width:100%;position:relative;transition:.3s ease 0s;background-attachment:fixed;background-size:cover}.about-container{width:100%;border:0;text-align:center;margin:0 auto}.header{width:100%;text-align:center;padding:20px 0;position:relative;background-color:rgba(226,66,66,.9)}.title{color:#fff;font-size:50px;font-weight:normal;font-family:"华文行楷";margin:0;letter-spacing:10px}.timeline{display:flex;margin:0 auto;flex-wrap:wrap;flex-direction:column;width:100%;position:relative;background-color:rgba(226,66,66,.9)}.content-title{font-weight:normal;font-size:35px;margin:-10px 0 0 0;transition:.4s;padding:0 10px;box-sizing:border-box;color:#fff}.timeline:before{position:absolute;left:50%;width:2px;height:100%;margin-left:-1px;content:"";background:rgba(255,255,255,.07)}.item{padding:100px 0;opacity:.3;filter:blur(1px);transition:.5s;box-sizing:border-box;width:calc(50% - 40px);display:flex;position:relative}.item:before{content:attr(data-text);text-align:left;letter-spacing:3px;width:500px;position:absolute;color:rgba(248,248,248,.8);font-size:13px;top:25%;right:-600px;font:900 45px "";letter-spacing:5px}.content-desc-right{position:absolute;right:-600px;bottom:160px;width:500px;height:200px;color:#ecc4c4;text-align:justify;font-size:24px;line-height:1.5}.content-desc-left{position:absolute;right:780px;bottom:160px;width:500px;height:200px;color:#ecc4c4;text-align:justify;font-size:24px;line-height:1.5}.item:nth-child(even){align-self:flex-end}.item:nth-child(even):before{right:auto;text-align:right;left:-90%;padding-left:0;border-left:none;padding-right:15px;width:500px}.item--active{opacity:1;transform:translateY(0);filter:blur(0px)}.item--active:before{transition:.3s all .2s;opacity:1}.item--active .content-title{margin:-20px 0 20px 0;font-size:50px;font-family:"华文行楷"}.img{background-size:cover;border-radius:10px;height:300px;width:600px;box-shadow:0 10px 15px rgba(0,0,0,.4)}.leader-introduce{display:flex;flex-direction:column;align-items:center;height:91vh;background:linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)),url("../../assets/about/back.jpg");background-size:cover}.leader-introduce h2{margin-top:20px;color:#fff;font-size:50px;font-weight:normal;font-family:"华文行楷";letter-spacing:10px}li{list-style:none}.leader-introduce .container{padding:20px;line-height:1.6;position:relative}.cards-container{display:flex;width:70vw;margin:0 auto}.cards{background-size:cover;background-position:center;height:450px;border-radius:50px;color:#fff;cursor:pointer;flex:.5;position:relative;margin:10px;transition:all .5s ease-in;font-family:"华文行楷"}.cards.active{flex:4;background-position:top;background-size:cover}.cards:nth-child(1).active{background-position:20% 10%}.cards:nth-child(3).active{background-position:20% 40%}.cards h3{font-size:24px;position:absolute;bottom:-80px;left:20px;margin:0;opacity:0}.cards.active h3{opacity:1;transition:opacity .3s ease-in .4s}