.messages-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(90deg, #e0c3fc 0%, #8ec5fc 100%);
}

.messages {
  margin-top: 100px;
  width: 80%;
  padding: 50px 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.6),
    rgba(255, 255, 255, 0.3),
    rgba(255, 255, 255, 0.2)
  );
  background-color: rgba(255, 255, 255, 0.266);
  border-radius: 40px;
  box-shadow: 0 10px 50px #00000019;
}

.form {
  display: flex;
  justify-content: center;
  position: relative;
  flex-direction: column;
  width: 100%;
  padding-bottom: 90px;
}

input:focus,
textarea:focus {
  outline: none;
}

input,
textarea {
  border: none;
  color: #000;
  font: 900 30px '';
  border-radius: 10px;
  padding: 30px;
}

#username {
  height: 40px;
}

#message {
  height: 150px;
}

.messages h1 {
  width: 100%;
  text-align: center;
  margin-bottom: 30px;
  font-size: 40px;
  background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: 10px;
}

#submitBtn {
  position: absolute;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(90deg, #e0c3fc 0%, #8ec5fc 100%);
  border: none;
  font-size: 30px;
  letter-spacing: 5px;
  width: 150px;
  height: 80px;
  border-radius: 50px;
  transition: all 0.1s ease-in-out;
}

#messageBoard {
  width: 100%;
  text-align: left;
}
#submitBtn:active {
  transform: scale(0.9);
}
@keyframes messageFadeIn {
  to {
    opacity: 1;
  }
}

.message {
  width: 100%;
  margin: 10px;
  padding: 10px;
  position: relative;
  opacity: 0;
  animation: messageFadeIn 0.5s ease forwards;
  background-image: linear-gradient(90deg, #8ec5fc 0%, #e0c3fc 100%);
  background-color: #fff;
  margin: 70px 0;
  border-radius: 10px;
  box-shadow: 0 10px 20px #00000026;
  text-shadow: 0px 0px 20px #ffffff;
}

.message-info {
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 30px;
  position: relative;
}

.info {
  transform: translateY(-30px);
}

.info img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 10px #fff solid;
}

strong {
  position: absolute;
  width: 800px;
  letter-spacing: 3px;
  top: 40px;
  left: 120px;
}

.message-info span {
  position: absolute;
  top: 10px;
  right: 10px;
}

.message .content {
  font-size: 30px;
  margin: 30px;
  width: 95%;
}
/* 其他样式保持不变 */

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.popup-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.popup-content h2 {
  margin-bottom: 20px;
}

.popup-content button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}
