.introBack{width:100%;height:100vh;overflow:hidden;position:relative;margin:0}.introBack .page{transform:translateX(-110%);position:absolute;width:50%;height:100%;transition:.5s;background-size:cover}.introBack .button{display:block;width:10%;color:#fff;font-size:30px;font-family:"华文行楷";border-bottom:#fff solid 4px;text-align:center;position:absolute;top:610px;margin:50px;left:calc(55% + var(--i)*25%);z-index:0;transition:.3s}.introBack .page_content{box-sizing:border-box;height:100%;padding:50px;color:#fff;position:absolute;left:100%;display:none;animation-name:fade;animation-duration:.7s;animation-iteration-count:1;opacity:1;text-align:justify}.page_content h1{font-family:"华文行楷"}@keyframes fade{0%{opacity:0}50%{opacity:0;transform:translateY(-50px)}100%{opacity:1;transform:translateY(0px)}}.introBack input{display:none}.introBack input:checked+.page{transform:translateX(0px);transition:.4s}.introBack input:checked+.page .page_content{width:100%;display:block}.introBack .page:nth-child(3){background-image:url("../../assets/intro-back/intro.jpeg");background-size:cover}.introBack .page:nth-child(3) .page_content{background-color:#f56a6a}.introBack .page:nth-child(6){background-image:url("../../assets/intro-back/back.jpg");background-size:cover}.introBack .page:nth-child(6) .page_content{background-color:#f56a6a}.introBack .page h1{font-size:70px}.introBack .page p{font-size:30px;padding:10px;background-color:rgba(0,0,0,.3);border-radius:10px;margin:30px 0}.introBack .page span{font-size:26px;letter-spacing:2px;color:#383838}.introBack label:hover{cursor:pointer;border-bottom:solid 4px #fd2424;color:#fd2424}