*{margin:0;padding:0;box-sizing:border-box}section{padding:100px 200px}.home{position:relative;width:100%;min-height:100vh;display:flex;justify-content:center;flex-direction:column}.home video{z-index:0;position:absolute;top:0;left:0;width:100%;height:100%;object-fit:cover}.home .cut_content{z-index:888;color:#f3dfdf;width:70%;margin-top:50px;display:none}.home .cut_content.active{display:block}.home .cut_content h1{font-size:4rem;font-weight:900;text-transform:uppercase;letter-spacing:5px;line-height:75px;margin-bottom:40px;font-family:"华文行楷";opacity:1;animation:fate-out 15s ease-in-out infinite}.home .cut_content h1 span{font-size:1.2em;font-weight:600}.slider-navigation{z-index:888;position:relative;display:flex;justify-content:center;align-items:center;transform:translateY(80px);margin-bottom:12px;gap:20px}.slider-navigation .nav-btn{width:12px;height:12px;background:#fff;border-radius:50%;cursor:pointer;box-shadow:0 0 2px rgba(255,255,255,.5);transition:width .3s ease,height .3s ease,background-color .3s ease}.slider-navigation .nav-btn.active{background:#2696e9}.slider-navigation .nav-btn:hover{transform:scale(1.2)}.video-slide{position:absolute;clip-path:circle(0.01% at 0 50%)}.video-slide.active{clip-path:circle(150% at 0 50%);transition:.5s ease}@media(max-width: 1040px){header{padding:12px 20px}section{padding:100px 20px}}@keyframes fate-out{0%{opacity:1}50%{opacity:0}100%{opacity:1}}