*{margin:0;padding:0;box-sizing:border-box}header{z-index:999;display:flex;position:absolute;top:0;left:0;width:100%;justify-content:space-between;padding:15px 100px;transition:height .3s ease,padding .3s ease;align-items:center;position:fixed;height:60px}header.scrolled{height:80px;padding:15px 100px}.home-name{color:#f3e1e1;font-size:30px;font-family:"华文行楷";font-weight:700;text-transform:uppercase;text-decoration:none;transition:font-size .3s ease}.home-name.scrolled{font-size:35px}header .navigation{position:relative}header .navigation .navigation-items a{position:relative;color:#fff;font-weight:800;font-size:25px;text-decoration:none;margin-left:30px;transition:font-size .3s ease;letter-spacing:5px;font-family:"华文行楷"}header .navigation .navigation-items a.scrolled{font-size:28px}header .navigation .navigation-items a::before{content:"";position:absolute;bottom:0;left:0;background-color:red;width:0;height:3px;transition:width .3s ease}header .navigation .navigation-items a:hover:before{width:100%}header .navigation .navigation-items a:hover{color:red;font-weight:800}@media(max-width: 1040px){header{padding:12px 20px}header .navigation{display:none}.menu-btn{background-size:30px;width:40px;height:40px;cursor:pointer;transition:width .3s ease,height .3s ease,background-color .3s ease}.menu-btn.active{z-index:999;background-size:25px;transition:width .3s ease,height .3s ease,background-color .3s ease}}